.slider-wrapper {
    position: relative;
    width: 540px;
    height: 490px;
}

.slider-container {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.slider-item {
    position: absolute;
    inset: 0;
    font-size: 22px;
    height:100%;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.slider-item--active {
    visibility: visible;
    opacity: 1;
}

.slider-item img {

    width: 100%;
    height: 100%;
    object-fit: cover;
}

.btn-circle {

    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-slider,
.btn-slider {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: var(--white-color);
    opacity: 0.6;
    transition: 0.3s ease-in-out;
}

.btn-slider-next {
    left: calc(100% - 50px);
    
  
}

.btn-slider-prev {
    left: 10px;
}

.btn-slider-next:hover,
.btn-slider-prev:hover {
    opacity: 1;
}


@media screen and (max-width: 768px) {
    .slider-wrapper {
        max-width: 100%;
    }
}

