  height: 0;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.rating__label {
  flex: 0 0 20%;
  height: 100%;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.rating__label:before {
  /* content: url(../../img/icons/star-fill.svg); */
  flex: 0 0 20%;
  display: block;
}

.rating__label:hover,
.rating__label:hover ~ .rating__label,
.rating__label:checked ~ .rating__label:hover {
  -webkit-filter: brightness(0) saturate(100%) invert(93%) sepia(7%) saturate(4053%) hue-rotate(315deg)
